<template>
    <aside class="cell medium-2 show-for-medium">
        <card class="filter-types" v-for="(strategy, key, indexMacro) in funds_strategies" :key="indexMacro">
            <div class="app-checkbox app-checkbox-select-all" :style="collapsed_filter[indexMacro].collapse ? '' : 'margin: 0;'">
                <input 
                    type="checkbox"
                    :id="`select_all-${indexMacro}`" 
                    v-model="selected_all[indexMacro].select"
                    @change="selectAll(strategy, indexMacro)">
                <label :for="`select_all-${indexMacro}`">{{ key }}</label>
                <button :class="`btn-collapse ${collapsed_filter[indexMacro].collapse ? 'up' : 'down'}`" @click="collapseFilter(indexMacro)"></button>
            </div>
            <div v-show="collapsed_filter[indexMacro].collapse">
                <div class="app-checkbox" v-for="(item, indexMain) in strategy" :key="indexMain">
                    <input 
                        type="checkbox"
                        :id="indexMain" 
                        @change="selectMainStrategy(strategy, indexMacro)"
                        v-model="item.isChecked">
                    <label :for="indexMain">{{ item.main }}</label>
                </div>
            </div>
            
        </card>
    </aside>
</template>

<script>
export default {
    data() {
        return {
            selected_all: [],
            collapsed_filter: []
        }
    },
    computed: {
        funds_strategies() {
            return this.$store.state.filters.strategies;
        }
    },
    methods: {
        selectAll(strategy, i) {
            strategy.forEach((item) => {
                item.isChecked = this.selected_all[i].select;
            });
        },

        collapseFilter(i) {
            this.collapsed_filter[i].collapse = !this.collapsed_filter[i].collapse;
        },

        selectMainStrategy(strategy, i) {
            this.selected_all[i].select = strategy.every(item => item.isChecked);
        }
    },
    created(){
        const strategies = Object.entries(this.$store.state.filters.strategies)
        strategies.forEach(() => {
            this.collapsed_filter.push({collapse: true});
            this.selected_all.push({select: true});
        })
    }
}
</script>