<template>
    <table class="funds-table" cellspacing="0">

        <!-- THEAD -->
        <thead>
            <tr class="show-for-medium">
                <th>Fundo</th>
                <th class="text-right">Data da cota</th>
                <th class="text-right">Ms (%)</th>
                <th class="text-right">2016 (%)</th>
                <th class="text-right">12 M (%)</th>
                <th class="text-right">Aplicao mnima (R$)</th>
                <th class="text-right">Prazo do resgate</th>
                <th>Aplicar</th>
            </tr>
        </thead>

        <!-- TBODY - CADA TBODY  UM FUNDO -->
        <tbody v-for="(fund, index) in model" :key="index" >
            <tr 
                :class="`funds-table-tr show-for-medium ${fund.is_closed_to_capture ? 'fund-disabled' : ''} ${fund.id == fund_id ? 'actived' : ''}`"
                @click="expandFundDetail(fund.id)">

                <td :class="`fund-risk-${fund.specification.fund_risk_profile.score_range_order}`">
                    <strong>
                        {{fund.simple_name}}
                        <app-tooltip 
                            text="Fundo para investidor qualificado" 
                            dir="top">
                            <i class="icon-star" v-if="fund.specification.is_qualified"></i>
                        </app-tooltip>
                    </strong><br/>
                    <small>{{fund.specification.fund_type}}</small>
                </td>
                <td class="text-right">{{fund.quota_date}}</td>
                <td class="text-right">{{fund.profitabilities.month | currency}}</td>
                <td class="text-right">{{fund.profitabilities.year | currency}}</td>
                <td class="text-right">{{fund.profitabilities.m12 | currency}}</td>
                <td class="text-right">{{fund.operability.minimum_initial_application_amount | currency}}</td>
                <td class="text-right">
                    <app-tooltip 
                        :text="fund.operability.retrieval_quotation_days_str" 
                        dir="bottom"
                        background="lighten"
                        v-if="(fund.operability.retrieval_quotation_days_type != 'teis' && fund.operability.retrieval_quotation_days_type != 'corridos') || fund.operability.retrieval_special_type != ''">
                        <i class="icon-information"></i>
                    </app-tooltip>
                    <p class="margin-bottom-0" v-else>{{fund.operability.retrieval_quotation_days_str | shortName}}</p>
                </td>
                <td class="text-center">
                    <a href="#" class="link-fund-apply icon-reply" v-if="!fund.is_closed_to_capture"></a>
                    <i class="icon-block" v-else></i>
                </td>
                
            </tr>

            <!-- DETALHES DO FUNDO -->
            <funds-table-detail :model="fund" v-if="fund.id == fund_id" />

            <!-- DETALHES DO FUNDO - VERSAO MOBILE -->
            <funds-table-detail-mobile :model="fund" />
        </tbody>
        
    </table>
</template>

<script>

import FundsTableDetail from "./FundsTableDetail"
import FundsTableDetailMobile from "./FundsTableDetailMobile"

export default {
    components: { FundsTableDetail, FundsTableDetailMobile },
    props: {
        model: {
            required: true
        }
    },
    data() {
        return {
            fund_id: null
        }
    },
    filters: {
        // Filtro para remover a descrio que vem entre parnteses
        shortName(value) {
            return value.replace(/(?<=\s).*/, '');
        }
    },
    methods: {
        expandFundDetail(id){
            if(this.fund_id != id) this.fund_id = id
            else this.fund_id = null
        }
    }
}
</script>