<template>
    <div class="home">
        <div class="loader-container" v-if="loading">
            <app-loader />
        </div>
        <div class="grid-x grid-margin-x" v-if="!loading">
            <div class="cell medium-10">

                <!-- SEARCH E FILTROS -->
                <funds-search />

                <!-- TABELA DE FUNDOS -->
                <card>
                    <funds-table :model="filtered_funds" />
                    <h6 class="padding-3 text-center" v-if="!loading && !filtered_funds.length">O fundo buscado no est disponvel no momento.</h6>
                </card>

            </div>

            <!-- SIDEBAR -->
            <funds-sidebar />

        </div>

    </div>
</template>

<script>
import FundsTable from '@/components/FundsTable'
import FundsSearch from '@/components/FundsSearch'
import FundsSidebar from '@/components/FundsSidebar'
import { contains } from '@/mixins/strings'

export default {
    name: 'home',
    components: {FundsTable, FundsSearch, FundsSidebar},
    data() {
        return {
            loading: false
        }
    },
    computed: {
        funds() {
            return this.$store.state.funds;
        },

        filters() {
            return this.$store.state.filters;
        },

        filtered_funds() {
            return this.$store.state.funds.filter(fund => {
                return this.filterBySimpleName(fund, this.filters.simple_name) &&
                    this.filterByQuotationDays(fund) &&
                    this.filterByApplicationValue(fund) &&
                    this.filterByStrategy(fund)
            })
        },

        filteredStrategies() {
            return Object.values(this.$store.state.filters.strategies).reduce((acc, item) => {
                const items = item
                .filter(({ isChecked }) => !isChecked)
                .map(({ main }) => main)

                acc = [...acc, ...items]

                return acc
            }, [])
        }
    },
    methods: {
        filterBySimpleName(fund, term) {
            return contains(fund.simple_name, term)
        },

        filterByStrategy(fund) {
            return !this.filteredStrategies.length ||
                !this.filteredStrategies.includes(fund.specification.fund_main_strategy.name)
        },

        filterByQuotationDays(fund) {
            return fund.operability.retrieval_quotation_days <= parseInt(this.filters.retrieval_quotation_days)
        },

        filterByApplicationValue(fund) {
            return parseInt(fund.operability.minimum_initial_application_amount) <= parseInt(this.filters.max_application_value)
        }
    },
    mounted() {
        this.$store.dispatch('fetch_funds', { app: this })
    }
}
</script>

<style lang="scss">

    .loader-container {
        text-align: center;
        padding: 20px 0;
    }

</style>